:root {
    --bs-border-color: var(--tc-system-stroke) !important;
}

// background colors
.bg-light {
    background-color: var(--tc-bg-light) !important;
}

.bg-green {
    background-color: var(--tc-system-green) !important;
}

.bg-green-3 {
    background-color: var(--tc-system-green-3) !important;
}

// text colors
.text-dark {
    color: var(--tc-system-black) !important;
}

.text-dark-2 {
    color: var(--tc-system-black-2) !important;
}

.text-yellow {
    color: var(--tc-system-yellow) !important;
}

.text-green-3 {
    color: var(--tc-system-green-3);
}

.text-secondary-2 {
    color: #afafaf;
}

.btn-text {
    font-family: var(--tc-btn-font-family);
    font-size: 14px;
    font-style: normal;
    font-weight: 600;
    line-height: 100%;
    /* 14px */
    text-transform: uppercase;
}

.content-top {
    .line {
        width: 20px;
        height: 1px;
        opacity: 0.5;
        background-color: var(--tc-theme-primary);
    }
}

.fill-primary {
    path {
        fill: var(--tc-theme-primary) !important;
    }

    fill: var(--tc-theme-primary) !important;
}

.fill-white {
    path {
        fill: var(--tc-system-white) !important;
    }

    fill: var(--tc-system-white) !important;
}

.fill-dark {
    path {
        fill: var(--tc-system-black) !important;
    }

    fill: var(--tc-system-black) !important;
}

.fill-green-3 {
    path {
        fill: var(--tc-system-green-3) !important;
    }

    fill: var(--tc-system-green-3) !important;
}

.stroke-primary {
    path {
        stroke: var(--tc-theme-primary) !important;
    }

    circle {
        stroke: var(--tc-theme-primary) !important;
    }
}

.stroke-green-3 {
    path {
        stroke: var(--tc-system-green-3) !important;
    }

    circle {
        stroke: var(--tc-system-green-3) !important;
    }
}

// variables end
.backdrop-filter-20 {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(20px);
}

.backdrop-filter-50 {
    background: rgba(255, 255, 255, 0.1);
    backdrop-filter: blur(50px);
}

.mask-image {
    mask-image: linear-gradient(to right, rgba(0, 0, 0, 0), rgba(0, 0, 0, 1) 20%, rgba(0, 0, 0, 1) 80%, rgba(0, 0, 0, 0));
}

.mask-image-2 {
    mask-image: linear-gradient(-270deg, #fff 14.77%, rgba(255, 255, 255, 0) 100%);
}

.bg-dark {
    --bs-dark-rgb: 41, 41, 41;
}

.bg-dark-2 {
    background: var(--tc-system-black-2);
}

.filter-invert {
    filter: invert(1);
}

.contact-map {
    max-height: 700px;

    iframe {
        height: 700px;
        width: 100%;
    }
}

.map {
    -webkit-filter: grayscale(75%);
    -moz-filter: grayscale(75%);
    -ms-filter: grayscale(75%);
    -o-filter: grayscale(75%);
    filter: grayscale(75%);
}
